import React from "react";

const Headers = () => {
    const data = [
        { id: 0, title: "" },
        { id: 1, title: "" },
        { id: 2, title: "" },
        { id: 3, title: ", " },
        { id: 4, title: "" },
        { id: 5, title: "" },
        { id: 6, title: " " }
    ];
    return (
        <tr>
            {data.map((header) => (
                <th scope="col" key={header.id}>
                    {header.title}
                </th>
            ))}
        </tr>
    );
};

export default Headers;
